/*----- start animate -----*/
@-webkit-keyframes fadeInUp {
0% {
    -webkit-transform: translate3d(0, 0.78125vw, 0);
            transform: translate3d(0, 0.78125vw, 0);
    opacity: 0;
}
100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
}
}
@keyframes fadeInUp {
0% {
    -webkit-transform: translate3d(0, 0.78125vw, 0);
            transform: translate3d(0, 0.78125vw, 0);
    opacity: 0;
}
100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    opacity: 1;
}
}

