    background: rgba(255, 0, 0, 0.75);
    -webkit-backdrop-filter: blur(5px);
}
@media screen and (min-width: 1000px) {
.i-header.is-opaque {
      background-color: #fff;
}
}
.i-header.is-opaque.is-active {
    background: rgba(255, 0, 0, 0.75);
    -webkit-backdrop-filter: blur(5px);
}
@media screen and (max-width: 1239px) {
.i-header__inner {
      max-width: 1000px;
