.i-header__logo {
        width: 82px;
}
}
@media screen and (min-width: 576px) {
.i-header__logo {
        width: 136px;
}
}
.i-header nav, .i-header__nav {
    float: right;
    position: relative;
}
.i-header nav {
    opacity: 0;
}
@media screen and (min-width: 1000px) {
.i-header nav {
        opacity: 1;
}
}
.i-header nav > a {
      float: left;
      position: relative;
      padding: 0 30px;
      font-size: 0.83333vw;
      color: #333;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) and (max-width: 1279px) and (min-width: 1000px) {
.i-header nav > a {
          font-size: 1.25vw;
}
}
@media screen and (max-width: 575px) {
.i-header nav > a {
          font-size: 3.73333vw;
}
}
@media screen and (max-width: 999px) and (min-width: 576px) {
.i-header nav > a {
          font-size: 2.13333vw;
}
}
.i-header nav > a.is-active {
        color: #94a6be;
}
@media screen and (max-width: 999px) {
.i-header nav {
        position: fixed;
        left: 0;
        right: 0;
        height: 73.33333vw;
        -webkit-transform: scale(1, 0);
                transform: scale(1, 0);
        -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
        -webkit-transition: -webkit-transform 350ms ease-out;
        transition: -webkit-transform 350ms ease-out;
        transition: transform 350ms ease-out;
        transition: transform 350ms ease-out, -webkit-transform 350ms ease-out;
}
.i-header nav.is-active {
          opacity: 1;
          -webkit-transform: scale(1, 1);
                  transform: scale(1, 1);
          background: rgba(255, 0, 0, 0.75);
          -webkit-backdrop-filter: blur(5px);
}
.i-header nav.is-active > a {
            opacity: 1;
            -webkit-transform: translate3d(0, 0, 0);
                    transform: translate3d(0, 0, 0);
}
.i-header nav.is-active > a:nth-child(1) {
              -webkit-transition-delay: 0s;
                      transition-delay: 0s;
}
.i-header nav.is-active > a:nth-child(2) {
              -webkit-transition-delay: 0.01s;
                      transition-delay: 0.01s;
}
.i-header nav.is-active > a:nth-child(3) {
              -webkit-transition-delay: 0.02s;
                      transition-delay: 0.02s;
}
.i-header nav.is-active > a:nth-child(4) {
              -webkit-transition-delay: 0.03s;
                      transition-delay: 0.03s;
}
.i-header nav.is-active > a:nth-child(5) {
              -webkit-transition-delay: 0.04s;
                      transition-delay: 0.04s;
}
.i-header nav.is-active > a:nth-child(6) {
              -webkit-transition-delay: 0.05s;
                      transition-delay: 0.05s;
}
.i-header nav > a {
          display: block;
          float: none;
          line-height: 12vw;
          position: relative;
          opacity: 0;
          -webkit-transform: translate3d(0, -100%, 0);
                  transform: translate3d(0, -100%, 0);
          -webkit-transition: all 500ms ease-in;
          transition: all 500ms ease-in;
}
.i-header nav > a.noborder:after {
            border: none;
}
.i-header nav > a:after {
            content: " ";
            position: absolute;
            left: 0;
            top: 0;
            width: 200%;
            height: 200%;
            border-bottom: 1px solid #333;
            color: #333;
            border-radius: 0;
            -webkit-transform-origin: left top;
                    transform-origin: left top;
            -webkit-transform: scale(0.5);
                    transform: scale(0.5);
            left: 30px;
            bottom: 0;
}
}
@media screen and (max-width: 999px) and (max-width: 575px) {
